"use client";
import React, { useRef } from "react";
import { RiMenu3Line } from "react-icons/ri";
import Header from "./Header";
import Link from "next/link";

const Navbar = () => {
  const menu = useRef();


  return (
    <nav className="flex items-center justify-between px-6 py-6 relative bg-green-900">
     
      <h1 className="font-bold text-2xl cursor-pointer text-gray-950">
        Guide-<span className="text-white">U</span>
      </h1>

      
      <div
        ref={menu}
        className="absolute top-[100%] left-[-100%] transition-all duration-300 ease-in-out
        flex flex-col md:flex-row gap-y-4 md:gap-x-10 items-start md:items-center
        md:static md:w-auto w-[200px] p-4 md:p-0 bg-green-100 md:bg-transparent z-50"
      >
        <Link href="/">Home</Link>
        <Link href="/about">About Us</Link>
        <Link href="/pricing">Pricing</Link>
        <Link href="/contact">Contact</Link>
        <Link href="/services">Services</Link>
        <Link href="/login" ><button className="bg-green-500 hover:bg-white p-2 rounded-2xl">Login</button></Link>

        

       
       

     
      </div>
    </nav>
  );
};

export default Navbar;
